* {
  box-sizing: border-box;
}

body {
  background-color: #efefef;
  color: #444;
  font-family: helvetica;
  font-size: 16px;
  line-height: 1.6;
  margin: 40px auto;
  max-width: 750px;
  padding: 0 10px;
}

h1, h3 {
  line-height: 1.25;
  margin: 12px 0 0;
  text-align: center;
}

h3 {
  font-weight: normal;
}

pre {
  background-color: #282828;
  color: #ffb000;
  overflow-y: scroll;
  padding: 12px;
}

form {
  margin: 24px 0;
  width: 100%;
}

.input-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.searchbox {
  background-color: #fafafa;
  border: 3px solid #444;
  font-size: 24px;
  height: 48px;
  padding: 0 10px;
  width: calc(100% - 90px);
}

.submit-btn {
  background-color: transparent;
  border: none;
  font-size: 24px;
  padding: 0;
  text-decoration: underline;
}

.submit-btn:hover {
  cursor: pointer;
}

.submit-btn:focus,
.searchbox:focus {
  outline: none;
}

